h1 {
  color: green;
  font-style: italic;
}

.note {
  color: gray;
  padding: 3px;
  font-size: 15px;
  list-style-type: disc;
}

.note button {
  margin-left: 10px;
}

.error {
  color: red;
  background: rgb(252, 251, 251);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
